import React, { useEffect, useState } from "react";
import {
    useTable,
    usePagination,
    useRowState,
    useBlockLayout,
    useRowSelect,
} from "react-table";

import EditableCell from "./EditableCell";
import SelectableCell from "./SelectableCell";
import IndeterminateCheckbox from "./IndeterminateCheckbox";
import i18n from "i18n/i18n";
import DisableEditCell from "./DisableEditCell";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { FaRegArrowAltCircleRight } from "react-icons/fa";

const defaultColumn = {
    Cell: EditableCell,
    Select: SelectableCell,
    DisableCell: DisableEditCell,
    width: 50,
};

function TablesSelectionNoiDung({
    columns,
    data,
    updateMyData,
    skipPageReset,
    isPagination,
    handleSelectedRow,
    tongSoTinChi = 0,
    isFooterSum = false,
    moveToSubject,
    ...restProps
}) {
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        prepareRow,
        footerGroups,
        rows,
        state: { selectedRowIds, ...rest },
    } = useTable(
        {
            columns,
            data,
            defaultColumn,
            updateMyData,
        },
        usePagination,
        useRowState,
        useBlockLayout,
        useRowSelect,
        (hooks) => {
            hooks.visibleColumns.push((columns) => [
                {
                    id: "selection",
                    Header: ({ getToggleAllRowsSelectedProps }) => (
                        <div>
                            <IndeterminateCheckbox
                                {...getToggleAllRowsSelectedProps()}
                            />
                        </div>
                    ),
                    Cell: ({ row }) => (
                        <div>
                            <IndeterminateCheckbox
                                {...row.getToggleRowSelectedProps()}
                            />
                        </div>
                    ),
                },
                ...columns,
            ]);
        }
    );

    useEffect(() => {
        handleSelectedRow(Object.keys(selectedRowIds));
    }, [Object.keys(selectedRowIds).length]);

    return (
        <>
            <div {...getTableProps()} className="table">
                <div>
                    {headerGroups.map((headerGroup) => (
                        <div
                            {...headerGroup.getHeaderGroupProps()}
                            className="tr"
                        >
                            {headerGroup.headers.map((column) => (
                                <div
                                    {...column.getHeaderProps()}
                                    className="th"
                                >
                                    {column.render("Header")}
                                </div>
                            ))}
                        </div>
                    ))}
                </div>

                <div {...getTableBodyProps()}>
                    {rows.map((row, i) => {
                        prepareRow(row);
                        return (
                            <div {...row.getRowProps()} className="tr">
                                {row.cells.map((cell) => {
                                    let ComponentChild = () =>
                                        cell.render("Cell");
                                    if (cell?.column?.disabled) {
                                        ComponentChild = () =>
                                            cell.render(() => (
                                                <input
                                                    disabled
                                                    value={cell?.value}
                                                />
                                            ));
                                    } else if (cell?.column?.isSelect) {
                                        ComponentChild = () =>
                                            cell.render("Select", {
                                                options: cell?.column?.options,
                                                value: cell?.value,
                                            });
                                    } else if (cell?.column?.isValidate) {
                                        ComponentChild = () =>
                                            cell.render("Cell", {
                                                isValidate: true,
                                            });
                                    }
                                    else if (cell?.column?.isDisable) {
                                        ComponentChild = () =>
                                            cell.render("DisableCell");
                                    }
                                    else if (cell?.column?.isDropdown) {
                                        ComponentChild = () => <DropdownMoveTo cell={cell} {...restProps} moveToSubject={moveToSubject}/>;
                                    }
                                    return (
                                        <div
                                            {...cell.getCellProps()}
                                            className="td"
                                        >
                                            <ComponentChild />
                                        </div>
                                    );
                                })}
                                
                            </div>
                        );
                    })}
                    {isFooterSum && <div style={{ display: "flex", padding: "5px 0" }}>
                        <div style={{ width: "700px", textAlign: "right" }}>
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.CREDIT_TOTAL')}
                        </div>
                        <div style={{ marginLeft: "20px" }}>{tongSoTinChi}</div>
                    </div>}
                </div>
            </div>
        </>
    );
}

export default TablesSelectionNoiDung;

const DropdownMoveTo = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const toggle = () => setIsOpen(prevState => !prevState);
    // console.log(`props`, props?.listGomNhom[0])

    const handleMoveTo = (gomNhomSrc, llktDesc, gomNhomDesc) => {
        props?.moveToSubject(gomNhomSrc, llktDesc, gomNhomDesc)
    }

    const renderList = () => {
        return props?.listGomNhom?.map(lkkt => {
            return lkkt?.gomNhom?.map((gn, index) => {
                return (<DropdownItem onClick={() => handleMoveTo(props?.cell?.row?.original, lkkt, index)}>{`${lkkt?.ten} - ${gn?.loaiNhom}`}</DropdownItem>)
            })
        })
    }

    return(
        <Dropdown direction="right" isOpen={isOpen} toggle={toggle}>
            <DropdownToggle>
                <FaRegArrowAltCircleRight />
            </DropdownToggle>
            <DropdownMenu>
                {renderList()}
            </DropdownMenu>
        </Dropdown>
    )
}