import React, { useEffect, useState } from "react";
import { FormGroup, Label, Button, Form, Input } from "reactstrap";
import InputSelectSearchable from "components/common/InputSelectSearchable/InputSelectSearchable";
import { toast } from "react-toastify";
import { LOAI_HP } from "../contantsCTDT";
import i18n from "i18n/i18n";

// const emptyLoaiKhoiKienThuc = {
//     maLoaiKhoiKienThuc: "",
//     ten: "",
//     tongTinChi: 0,
//     noidung: "",
//     gomNhom: [],
// };

// const initialValues = {
//     ten: "",
//     loaiKhoiKienThuc: [
//         {
//             maLoaiKhoiKienThuc: "",
//             ten: "",
//             tongTinChi: 0,
//             noidung: "",
//             gomNhom: [],
//         },
//     ],
// };

const tranformData = (data) => {
    if (data.length) {
        const newData = data.map((item, index) => {
            return {
                value: item.id || "NO_ID",
                label: item.ten,
                id: item?.id || "NO_ID",
                index,
            };
        });
        return newData;
    }
    return [];
};

const CreateKhoiKienThuc = (props) => {
    const [listKhoiKienThuc, setListKhoiKienThuc] = useState(
        tranformData(props.khoiKienThuc)
    );
    const [khoiKienThucSelected, setKhoiKienThucSelected] = useState(null);
    const [loaiKhoiKienThuc, setLoaiKhoiKienThuc] = useState("");
    const [gomNhomList, setGomNhomList] = useState([
        {
            maGN: null,
            loaiNhom: "BB",
            soTCBB: 0,
            tieuDe: null,
            stt: 1,
            chiTietGomNhom: [],
        },
    ]);
    const [error, setError] = useState({ ten: "", loaiKhoiKienThuc: "" });

    const handleChangeKhoiKienThuc = (selectedOption) => {
        setKhoiKienThucSelected(selectedOption);
    };

    const addNewGomNhom = () => {
        const cloneGomNhom = [...gomNhomList];
        cloneGomNhom.push({
            maGN: null,
            loaiNhom: "BB",
            soTCBB: 0,
            tieuDe: null,
            stt: cloneGomNhom.length + 1,
            chiTietGomNhom: [],
        });
        setGomNhomList(cloneGomNhom);
    };

    const handleOnChangeTieuDeGomNhom = (e) => {
        const { value, id } = e.target;
        if (id) {
            const inputIndex = id?.split("-")[1];
            const cloneGomNhomList = [...gomNhomList];
            cloneGomNhomList[inputIndex] = {
                ...cloneGomNhomList[inputIndex],
                tieuDe: value === '' ? null : value,
            };
            setGomNhomList(cloneGomNhomList);
        }
    };

    const handleOnChangeSoTinChiGomNhom = (e) => {
        const { value, id } = e.target;

        if (id) {
            const inputIndex = id?.split("-")[1];
            const cloneGomNhomList = [...gomNhomList];
            cloneGomNhomList[inputIndex] = {
                ...cloneGomNhomList[inputIndex],
                soTCBB: value,
            };
            setGomNhomList(cloneGomNhomList);
        }
    };

    const handleChangeLoaiNhom = (selectedOption, index) => {
        if (index) {
            const inputIndex = index?.split("-")[1];
            const cloneGomNhomList = [...gomNhomList];
            cloneGomNhomList[inputIndex] = {
                ...cloneGomNhomList[inputIndex],
                loaiNhom: selectedOption?.value || `${i18n.t('FLOW_CREATE_CTDT_DETAIL.LOAI_HP.BB')}`,
            };
            setGomNhomList(cloneGomNhomList);
        }
    };

    const onSubmit = () => {
        if (typeof (khoiKienThucSelected?.index) !== "undefined" && khoiKienThucSelected?.index !== null && loaiKhoiKienThuc) {
            const tongTinChiLKK = gomNhomList?.reduce((total, cur) => {
                return (total += Number(cur?.soTCBB || 0));
            }, 0);
            const loaiKhoiKienThucSubmit = {
                maLoaiKhoiKienThuc: "",
                noidung: "",
                ten: loaiKhoiKienThuc,
                tongTinChi: tongTinChiLKK,
                gomNhom: gomNhomList,
            };
            props.submit(khoiKienThucSelected.index, loaiKhoiKienThucSubmit);
        } else {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.ERROR'));
        }
    };

    return (
        <div>
            <form>
                <FormGroup>
                    <div>
                        <Label>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.KNOWLEDGE_BLOCK')}`}</Label>
                        <div className="error_message">
                            {error.error || null}
                        </div>

                        <InputSelectSearchable
                            value={khoiKienThucSelected}
                            options={listKhoiKienThuc}
                            onChange={handleChangeKhoiKienThuc}
                            className="basicinfo_input"
                        />
                    </div>
                </FormGroup>
                <FormGroup>
                    <div>
                        <Label>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.TYPE_KNOWLEDGE')}`}</Label>
                    </div>
                    <div>
                        <Input
                            // key={`input-${index}`}
                            // name={item.name}
                            // id={`input-${index}`}
                            placeholder={`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.PLACE_HOLDER_TYPE_KNOWLEDGE')}`}
                            value={loaiKhoiKienThuc || ""}
                            onChange={(e) =>
                                setLoaiKhoiKienThuc(e?.target?.value)
                            }
                        />
                    </div>
                </FormGroup>
                <FormGroup>
                    <div>
                        <Label>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.LIST_GROUP')}`}</Label>
                    </div>
                    <div>
                        {gomNhomList.map((item, index) => (
                            <div style={{ marginTop: "10px" }}>
                                <div>
                                    <Input
                                        key={`input-${index}`}
                                        // name={item.name}
                                        id={`input-${index}`}
                                        placeholder={`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.PLACE_HOLDER_GROUP_TITLE')}`}
                                        value={item?.tieuDe || ""}
                                        onChange={handleOnChangeTieuDeGomNhom}

                                    />
                                </div>

                                <div style={{ marginTop: "10px" }}>
                                    <InputSelectSearchable
                                        value={{
                                            value: item?.loaiNhom,
                                            label: item?.loaiNhom,
                                        }}
                                        placeholder={`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.PLACE_HOLDER_GROUP_TYPE')}`}
                                        options={LOAI_HP}
                                        onChange={(selectedOption) =>
                                            handleChangeLoaiNhom(
                                                selectedOption,
                                                `input-${index}`
                                            )
                                        }
                                    // className="basicinfo_input"
                                    />
                                </div>

                                <div style={{ marginTop: "10px" }}>
                                    <Input
                                        type="number"
                                        key={`input-${index}`}
                                        // name={item.name}
                                        id={`input-${index}`}
                                        placeholder={`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.MAXIMUM_TOTAL_CREDIT')}`}
                                        value={item?.soTCBB}
                                        onChange={handleOnChangeSoTinChiGomNhom}
                                    />
                                </div>
                                <hr />
                            </div>
                        ))}
                    </div>
                </FormGroup>
            </form>
            <div className="modal_footer_btn">
                <div>
                    <Button onClick={addNewGomNhom} color="warning">
                        {`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_KNOWLEDGE_BLOCK.ADD_NEW_GROUP')}`}
                    </Button>
                </div>
                <div>
                    <Button color="success" onClick={onSubmit}>
                        {`${i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE')}`}
                    </Button>{" "}
                    <Button color="danger" onClick={props.toggleCreateKKT}>
                        {`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CANCEL')}`}
                    </Button>
                </div>
            </div>
        </div>
    );
};

export default CreateKhoiKienThuc;
