import React, { useEffect, useState } from "react";
import NoiDungChuongTrinh from "./NoiDungChuongTrinh";
import { Button } from "reactstrap";
import Loading from "components/common/loading/Loading";
import ModalCreateLoaiKhoiKienThuc from "./ModalCreateLoaiKhoiKienThuc";
// import cloneTemplateAPI from "api/common/cloneTemplate.api";
import { toast } from "react-toastify";
import ConfirmSubmit, { InputModal } from "../../../../components/admin/ConfirmModal/ConfirmModal";

import "./css/NoiDungChuongTrinhWrapper.css";
import monHocApi from "api/common/monHoc.api";
// import { LOAI_HP } from "../contantsCTDT";
import i18n from "i18n/i18n";
import { LOAI_HP } from "../contantsCTDT";

const emptyChiTietGomNhom = {
    idMH: null,
    ghiChu: "",
    monHoc: {
        ma: "",
        tenTiengViet: "",
        tenTiengAnh: "",
        soTinChi: 0,
        soTietLyThuyet: 0,
        soTietThucHanh: 0,
        soTietTuHoc: 0,
        id: null,
        isDeleted: false,
    },
};

const extractGroups = data => {
    let list = [];
    for (const kkt of data) {
        for (const lkkt of kkt?.loaiKhoiKienThuc) {
            list.push(lkkt);
        }
    }
    return list;
}

const NoiDungChuongTrinhWrapper = (props) => {
    const [dataContainer, setDataContainer] = useState(null);
    const [data, setData] = useState([]);
    // const [idCTNDT, setIdCTNDT] = useState("");
    // const [idCTNDTClone, setIdCTNDTClone] = useState("");
    const [isOpenCreateKKT, setIsOpenCreateKKT] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [isOpenModal, setIsOpenModal] = useState(false);
    const [defaultSubjects, setDefaultSubjects] = useState([]);
    // const [listSubjectUnGroup, setListSubjectUnGroup] = useState([]);
    const [listGroup, setListGroup] = useState([]);

    //* toggle modal create new loai khoi kien thuc
    const toggleCreateKKT = () => setIsOpenCreateKKT(!isOpenCreateKKT);

    //* add new a subject to loai khoi kien thuc
    // const addNewMonHoc = (kktIndex, lkktIndex, gomNhomIndex) => {
    //     const cloneData = [...data];
    //     cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[
    //         gomNhomIndex
    //     ].chiTietGomNhom.push(emptyChiTietGomNhom);
    //     setData(cloneData);
    // };

    //* handle edit value data
    const modifyData = async (kktIndex, lkktIndex, gomNhomIndex, newData) => {
        const cloneData = [...data];
        cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[
            gomNhomIndex
        ].chiTietGomNhom = [...newData];
        setData(cloneData);
    };

    const modifyDataContainer = async (listRemoved) => {
        const dataClone = props.getState("dataClone", { keHoachGiangDay: [] });

        const keHoachRemoved = dataClone?.keHoachGiangDay?.map(kh => {
            const newChiTietKeHoach = kh?.chiTietKeHoach?.filter(ctkh => {
                if (listRemoved.find(item => String(item) === String(ctkh?.chiTietGomNhom?.monHoc?.id))) {
                    // eslint-disable-next-line
                    return false;
                }
                return true;
            })
            return { ...kh, chiTietKeHoach: newChiTietKeHoach };
        });



        setDataContainer(old => {
            return { ...old, keHoachGiangDay: keHoachRemoved }
        });
    }

    //* add new a loai khoi kien thuc
    const addNewKhoiKienThuc = (index, loaiKhoiKienThuc) => {
        const cloneData = [...data];
        cloneData[index].loaiKhoiKienThuc = [
            ...cloneData[index].loaiKhoiKienThuc,
            loaiKhoiKienThuc,
        ];
        setData(cloneData);
        setIsOpenCreateKKT(false);
    };

    //* save data into state step
    // const handleSave = async () => {
    //     try {
    //         props.setState("dataClone", {...dataContainer, khoiKienThuc: data});
    //         // props.setState("noiDung", data);
    //         toast.success("SUCCESS");
    //         // props.next();
    //     } catch (error) {
    //         toast.error("Lu li");
    //         return;
    //     }
    // };

    //* get data clone from step state or call API
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const stateData = props.getState("dataClone", { khoiKienThuc: [], unGroup: [] });
            if (stateData?.khoiKienThuc) {
                setDataContainer(stateData);
                setData(stateData?.khoiKienThuc);
                // setListSubjectUnGroup(stateData?.unGroup || []);
                setListGroup(extractGroups(stateData?.khoiKienThuc))
                setIsLoading(false);
                return;
            }
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.ERROR_FETCH_DATA'));

            // const stateCauTrucChuongTrinh = props.getState(
            //     "cauTrucChuongTrinh"
            // );
            // if (stateCauTrucChuongTrinh) {
            //     setData(stateCauTrucChuongTrinh);
            //     setIsLoading(false);
            //     return;
            // }

            // const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
            // const { idCTNDTClone } = props.getState("step2", {
            //     idCTNDTClone: "",
            // });
            // if (idCTNDT && idCTNDTClone) {
            //     setIdCTNDT(idCTNDT);
            //     setIdCTNDTClone(idCTNDTClone);
            //     const data = await fetchTemplate({ idCTNDT, idCTNDTClone });
            //     setData(data);
            // } else {
            //     toast.error("Li, idCTNDT hoc idCTNDTClone rng");
            // }
            setIsLoading(false);
        })();
    }, []);

    //* get default list subjects
    useEffect(() => {
        (async () => {
            const results = await monHocApi.getAll({ limit: 20 });
            if (results?.contents) {
                setDefaultSubjects(results?.contents || []);
            }
        })();
    }, []);

    //* call API get clone data noi dung chuong trinh
    // const fetchTemplate = async (params) => {
    //     try {
    //         const result = await cloneTemplateAPI.getTemplateKKT(params);
    //         return result;
    //     } catch (error) {
    //         toast.error("ERROR");
    //         return [];
    //     }
    // };

    //* call API to create new noi dung chuong trinh
    const handleNoiDungSubmit = async () => {
        try {
            // const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
            // const { idCTNDTClone } = props.getState("step2", {
            //     idCTNDTClone: "",
            // });
            // if (data && idCTNDT && idCTNDTClone) {
            //     const results = await cloneTemplateAPI.postTemplateKKT(
            //         { idCTNDT, idCTNDTClone },
            //         data
            //     );
            //     props.setState("noiDung", data);
            //     setIsOpenModal(false);
            // }
            // if (listSubjectUnGroup?.length) {
            //     props.setState("dataClone", { ...dataContainer, khoiKienThuc: data, unGroup: listSubjectUnGroup });
            // } else {
            // }
            props.setState("dataClone", { ...dataContainer, khoiKienThuc: data });
            props.next();
            // toast.success(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_SUCCESS'));
        } catch (error) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_ERROR'));
        }finally{
            setIsOpenModal(false);
        }
    };

    // const addItemToGroup = (subject, type = "BB") => {
    //     const isExist = listSubjectUnGroup?.find((sub) => {
    //         return String(sub?.idMH) === String(subject?.idMH)
    //     });

    //     if (isExist) {
    //         const newList = listSubjectUnGroup?.map((sub) => {
    //             if (String(sub?.idMH) === String(subject?.idMH)) {
    //                 return { ...subject, gomNhom: { loaiNhom: type } }
    //             }
    //             return sub;
    //         });
    //         setListSubjectUnGroup(newList);
    //         return;
    //     }

    //     setListSubjectUnGroup(old => {
    //         return [...old, { ...subject, gomNhom: { loaiNhom: type } }];
    //     })
    // }

    //* open modal confirm submit
    const openConfirmSubmit = () => {
        setIsOpenModal(true);
    };

    //* contents of confirm modal
    const bodyConfirmModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    const deleteGroups = async (itemIndex, kktIndex, lkktIndex) => {
        const cloneData = [...data];
        let arrDeleted = [];
        let isDeletedGomNhom = false;
        if(typeof(lkktIndex) !== "undefined" && lkktIndex !== null ){
            isDeletedGomNhom = true;
            arrDeleted = cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom?.splice(itemIndex, 1);
        }else{
            const parent = cloneData[kktIndex].loaiKhoiKienThuc[itemIndex];
            arrDeleted = cloneData[kktIndex].loaiKhoiKienThuc?.splice(itemIndex, 1);
        }
        const keHoachGiangDay = dataContainer?.keHoachGiangDay || [];
        if(isDeletedGomNhom){
            const newKeHoach = keHoachGiangDay?.map(kh => {
                const newChiTietKeHoach = kh?.chiTietKeHoach?.filter(ctkh => {
                    const isExist = arrDeleted[0]?.chiTietGomNhom?.find(item => {
                        if(String(ctkh?.chiTietGomNhom?.monHoc?.id) === String(item?.idMH)){
                            return true;
                        }
                        return false;
                    });
                    if(isExist){
                        return false;
                    }
                    return true;
                });
                return {...kh, chiTietKeHoach: newChiTietKeHoach};
            })
            setDataContainer(old => {
                return {...old, keHoachGiangDay: newKeHoach};
            })
        }else{
            const newKeHoach = keHoachGiangDay?.map(kh => {
                const newChiTietKeHoach = kh?.chiTietKeHoach?.filter(ctkh => {
                    for (const gomNhom of arrDeleted[0]?.gomNhom) {
                        for (const ctgn of gomNhom?.chiTietGomNhom) {
                            if(String(ctgn?.monHoc?.id) === String(ctkh?.chiTietGomNhom?.monHoc?.id)){
                                return false;
                            }
                        }
                    }
                    
                    return true;
                });
                return {...kh, chiTietKeHoach: newChiTietKeHoach};
            })
            setDataContainer(old => {
                return {...old, keHoachGiangDay: newKeHoach};
            })
        }
        setListGroup(extractGroups(cloneData));
        setData(cloneData);
    }

    const addGomNhom = (kktIndex, lkktIndex) => {
        const cloneData = [...data];
        if(typeof(lkktIndex) !== "undefined" && lkktIndex !== null ){
            const stt = cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom?.length;
            cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom?.push({
                maGN: null,
                loaiNhom: "BB",
                soTCBB: 0,
                tieuDe: null,
                stt: stt + 1,
                chiTietGomNhom: [],
            });
            setData(cloneData);
            setListGroup(extractGroups(cloneData));
        }
    }

    const changeTypeGroup = (e, kktIndex, lkktIndex, gomNhomIndex) => {
        const cloneData = [...data];
        if(typeof(lkktIndex) !== "undefined" && lkktIndex !== null && typeof(gomNhomIndex) !== "undefined" && gomNhomIndex !== null ){
            const item = cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[gomNhomIndex];
            cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[gomNhomIndex] = {
                ...item,
                loaiNhom: e?.target?.value
            };
            setData(cloneData);
            setListGroup(extractGroups(cloneData));
        }
    }

    const changeTieuDe = (e, kktIndex, lkktIndex, gomNhomIndex) => {
        const cloneData = [...data];
        if(typeof(lkktIndex) !== "undefined" && lkktIndex !== null && typeof(gomNhomIndex) !== "undefined" && gomNhomIndex !== null ){
            const item = cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[gomNhomIndex];
            cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[gomNhomIndex] = {
                ...item,
                tieuDe: e?.target?.value
            };
            setData(cloneData);
        }
    }

    const moveToSubject = (gomNhomSrc, lkktDesc, gomNhomDesc) => {

        // console.log(" ~ file: TablesSelectionNoiDung.js ~ line 172 ~ handleMoveTo ~ lkktDesc", lkktDesc)
        // console.log(" ~ file: TablesSelectionNoiDung.js ~ line 172 ~ handleMoveTo ~ gomNhomDesc", gomNhomDesc)
        // console.log(" ~ file: TablesSelectionNoiDung.js ~ line 172 ~ handleMoveTo ~ gomNhomSrc", gomNhomSrc)
        const cloneData = [...data];
        for (const kkt of cloneData) {
            for (const lkkt of kkt?.loaiKhoiKienThuc) {
                for (const gn of lkkt?.gomNhom) {
                    for (const ctgn of gn?.chiTietGomNhom) {
                        const index = gn?.chiTietGomNhom?.findIndex((ctgn, index) => {
                            if(String(ctgn?.idMH) === String(gomNhomSrc?.idMH)){
                                return true
                            }
                          })
                        if(index !== -1){
                            gn?.chiTietGomNhom.splice(index, 1);
                        }
                    }
                }
            }
        }

        for (const kkt of cloneData) {
            for (const lkkt of kkt?.loaiKhoiKienThuc) {
                if(String(lkkt?.ten) === String(lkktDesc?.ten)){
                    lkkt?.gomNhom?.[gomNhomDesc]?.chiTietGomNhom?.push(gomNhomSrc)
                }
            }
        }
        setData(cloneData);
    }

    const renderContent = () => {
        return (
            <div className="noi_dung_content">
                <ol>
                    {data.map((khoiKienThuc, kktIndex) => {
                        return (
                            <li key={`khoiKienThuc-${kktIndex}`}>
                                <p>{khoiKienThuc.ten}</p>
                                <ol>
                                    {khoiKienThuc.loaiKhoiKienThuc.map(
                                        (loaiKKT, lkktIndex) => (
                                            <li
                                                key={`khoiKienThuc-${kktIndex}loaiKKT-${kktIndex}-${loaiKKT.ten}`}
                                            >
                                                <span style={{display: "flex"}}>
                                                    <p>{loaiKKT.ten}</p>
                                                    <div className="btn_container" onClick={() => deleteGroups(lkktIndex,kktIndex)}>
                                                        <span class="btn-delete"><i class="fa fa-close"></i></span>
                                                    </div>

                                                    <div className="btn_container" onClick={() => addGomNhom(kktIndex, lkktIndex)}>
                                                        <span class="btn-plus"><i class="fa fa-plus"></i></span>
                                                    </div>
                                                </span>
                                                <ul>
                                                    {loaiKKT.gomNhom.map(
                                                        (
                                                            gomNhomItem,
                                                            gomNhomIndex
                                                        ) => (
                                                            <li key={`khoiKienThuc-${kktIndex}loaiKKT-${kktIndex}-gomNhomIndex-${gomNhomIndex}`}>
                                                                <div style={{display: "flex"}}>
                                                                    {gomNhomItem?.loaiNhom ? <div>
                                                                        <label>
                                                                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TYPE_GROUP')}
                                                                        </label>
                                                                        <select style={{marginLeft: "5px", border: 0}} value={gomNhomItem?.loaiNhom} onChange={(e) => changeTypeGroup(e, kktIndex, lkktIndex, gomNhomIndex)}>
                                                                            {
                                                                                LOAI_HP.map(item => (<option>{item?.value}</option>))
                                                                            }
                                                                        </select>
                                                                        {/* {
                                                                            gomNhomItem?.loaiNhom ? `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TYPE_GROUP')}: ${gomNhomItem?.loaiNhom}` : ''
                                                                        } */}
                                                                    </div> : ''}
                                                                    
                                                                    <div className="btn_container" onClick={() => deleteGroups(gomNhomIndex,kktIndex, lkktIndex)}>
                                                                        <span class="btn-delete"><i class="fa fa-close"></i></span>
                                                                    </div>
                                                                </div>
                                                                
                                                                <div style={{display: "flex"}}>
                                                                    <label style={{margin: "auto 5px"}}>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TITLE')}: `}</label>
                                                                    <input 
                                                                        value={gomNhomItem?.tieuDe || ''}
                                                                        onChange={(e) => changeTieuDe(e, kktIndex, lkktIndex, gomNhomIndex)}
                                                                        style={{width:"60%", border: 0}}/>
                                                                </div>
                                                                <NoiDungChuongTrinh
                                                                    modifyDataContainer={modifyDataContainer}
                                                                    // addItemToGroup={addItemToGroup}
                                                                    listGomNhom={listGroup}
                                                                    moveToSubject={moveToSubject}
                                                                    subjects={
                                                                        gomNhomItem.chiTietGomNhom
                                                                    }
                                                                    typeGroup={gomNhomItem?.loaiNhom}
                                                                    dataContainer={data}
                                                                    // addNewMonHoc={() =>
                                                                    //     addNewMonHoc(
                                                                    //         kktIndex,
                                                                    //         lkktIndex,
                                                                    //         gomNhomIndex
                                                                    //     )
                                                                    // }
                                                                    modifyData={(
                                                                        listSubject
                                                                    ) =>
                                                                        modifyData(
                                                                            kktIndex,
                                                                            lkktIndex,
                                                                            gomNhomIndex,
                                                                            listSubject
                                                                        )
                                                                    }
                                                                    columns={[
                                                                        {
                                                                            Header:
                                                                                `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.CODE')}`,
                                                                            accessor:
                                                                                "monHoc.ma",
                                                                            width: 100,
                                                                            isDisable: true
                                                                        },
                                                                        {
                                                                            Header:
                                                                                `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.VN_NAME')}`,
                                                                            accessor:
                                                                                "monHoc.tenTiengViet",
                                                                            width: 450,isDisable: true
                                                                        },
                                                                        {
                                                                            Header:
                                                                                `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.CREDIT_NUMBER')}`,
                                                                            accessor:
                                                                                "monHoc.soTinChi",
                                                                            width: 80,
                                                                            isDisable: true
                                                                        },
                                                                        {
                                                                            Header:
                                                                                `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.NUMBER_OF_CLASSES')}`,
                                                                            columns: [
                                                                                {
                                                                                    Header:
                                                                                        `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.TOTAL_THEORY_PERIOD')}`,
                                                                                    accessor:
                                                                                        "monHoc.soTietLyThuyet",
                                                                                    width: 80,
                                                                                    isDisable: true
                                                                                },
                                                                                {
                                                                                    Header:
                                                                                        `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.TOTAL_PRACTICE_PERIOD')}`,
                                                                                    accessor:
                                                                                        "monHoc.soTietThucHanh",
                                                                                    width: 80,
                                                                                    isDisable: true
                                                                                },
                                                                                {
                                                                                    Header:
                                                                                        `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.TOTAL_PRACTICE')}`,
                                                                                    accessor:
                                                                                        "monHoc.soTietTuHoc",
                                                                                    width: 80,
                                                                                    isDisable: true
                                                                                },
                                                                            ],
                                                                        },
                                                                        // {
                                                                        //     Header:
                                                                        //         "Loai hoc phan",
                                                                        //     accessor:
                                                                        //         "loaiNhom",
                                                                        //     width: 80,
                                                                        //     isSelect: true,
                                                                        //     options: LOAI_HP,
                                                                        // },
                                                                        {
                                                                            Header:
                                                                                `${i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.TABLE_HEADERS.NOTE')}`,
                                                                            accessor:
                                                                                "ghiChu",
                                                                            width: 100
                                                                        },
                                                                        {
                                                                            Header:
                                                                                ` `,
                                                                            width: 120,
                                                                            isDropdown: true
                                                                        },
                                                                    ]}
                                                                    defaultSubjects={
                                                                        defaultSubjects
                                                                    }
                                                                />
                                                            </li>
                                                        )
                                                    )}
                                                </ul>
                                            </li>
                                        )
                                    )}
                                </ol>
                            </li>
                        );
                    })}
                </ol>
            </div>
        );
    };

    return (
        <div>
            <div>
                <div className="cautrucchuongtrinh_btn">
                    {/* <Button onClick={handleSave} color="success">
                        Lu tt c
                    </Button> */}
                    <Button
                        onClick={() => setIsOpenCreateKKT(true)}
                        color="warning"
                    >
                        {i18n.t('FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.ADD_TYPE_BLOCK_KNOWLEDGE')}
                    </Button>
                    <Button onClick={openConfirmSubmit} color="success">
                        {i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_&_SUBMIT')}
                    </Button>
                </div>
            </div>
            {isLoading ? <Loading /> : renderContent()}

            <ModalCreateLoaiKhoiKienThuc
                khoiKienThuc={data}
                isOpenCreateKKT={isOpenCreateKKT}
                toggleCreateKKT={toggleCreateKKT}
                addNewKhoiKienThuc={addNewKhoiKienThuc}
            />

            <ConfirmSubmit
                modal={isOpenModal}
                toggle={() => setIsOpenModal(!isOpenModal)}
                headerTitle={""}
                bodyContent={bodyConfirmModal()}
                handleOkButtonOnClick={handleNoiDungSubmit}
                handleCancelButtonOnClick={() => setIsOpenModal(!isOpenModal)}
            />
        </div>
    );
};

export default NoiDungChuongTrinhWrapper;

