import React, { useEffect, useState } from "react";
import {
    Button,
    Modal,
    ModalFooter,
    ModalHeader,
    ModalBody,
    Label,
    Input,
    UncontrolledCollapse
} from "reactstrap";
import { useTranslation } from "react-i18next";
import * as lodash from "lodash";
// import Tooltip from "@material-ui/core/Tooltip";
import "./ChuanDauRaList.css";
import AsyncSelect from "react-select/async";
import { removeItemOnce } from "views/admin/FlowCreateCTDT/utils";
// import educationMajorOutputStandard from "api/common/education-major-output-standard";
import standardOutput from "api/common/standardOutput";
// import ConfirmModal from "components/admin/ConfirmModal/ConfirmModal";
import { FaEdit, FaEye, FaIndent, FaPlusCircle, FaTimesCircle } from "react-icons/fa";
import { checkExistInArray } from "views/admin/FlowCreateCTDT/utils";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const ChuanDauRaList = (props) => {
    const [data, setData] = useState(null);
    const [isModeEdit, setIsModeEdit] = useState(false);
    const [isOpenModalAdd, setIsOpenModalAdd] = useState(false);
    const [
        pathStandardOutputSelected,
        setPathStandardOutputSelected,
    ] = useState("");
    const [isCollapse, setIsCollapse] = useState(true);
    // const [parentSOSelected, setParentSOSelected] = useState("");

    useEffect(() => {
        setData(props.values);
    }, [props.values]);

    // onChange edit value
    const onHandEditItem = (e, pathObject) => {
        if (!isModeEdit) {
            return;
        }
        const value = e.target.value;
        let newData = { data: [...data] };
        newData = lodash.set(newData, pathObject, value);
        setData([...newData.data]);
        props.handleSetData([...newData.data]);
    };

    // store new chuan dau ra to data
    const confirmHandleAddItem = async (item) => {
        const isExist = await checkExistInArray(data, item?.chuanDauRa?.id);
        if(isExist){
            toast.error(`${i18n.t('FLOW_CREATE_CTDT_DETAIL.CHUAN_DAU_RA.CHUAN_DAU_RA_EXISTS')}`);
            return;
        }

        let newData = { data: [...data] };
        const children =
            lodash.get(newData, `${pathStandardOutputSelected}`) || [];
        // const lenChild = children?.length || 0;
        // let maItem = "";
        // if (parentSOSelected) {
        //     maItem = `${parentSOSelected}.${lenChild + 1}`;
        // } else {
        //     maItem = `${lenChild + 1}`;
        // }
        newData = lodash.set(newData, `${pathStandardOutputSelected}`, [
            ...children,
            {
                ...item,
            },
        ]);
        setData([...newData.data]);
        props.handleSetData([...newData.data]);
        setIsOpenModalAdd(false);
    };

    // open modal add new chuan dau ra
    const handleAddItem = (pathObject, parent = "") => {
        setPathStandardOutputSelected(pathObject);
        // setParentSOSelected(parent);
        setIsOpenModalAdd(true);
    };

    // close modal add chuan dau ra
    const handleCloseAddStandardOutput = () => {
        setPathStandardOutputSelected("");
        // setParentSOSelected("");
        setIsOpenModalAdd(false);
    };

    // delete chuan dau ra
    const handleDeleteItem = (pathParentObject, indexItem) => {
        let newData = { data: [...data] };
        const parent = lodash.get(newData, `${pathParentObject}`);
        const newParent = removeItemOnce(
            parent,
            typeof indexItem === "number" ? indexItem : Number(indexItem)
        );
        newData = lodash.set(newData, `${pathParentObject}`, newParent);
        setData([...newData.data]);
        props.handleSetData([...newData.data]);
    };

    const togglerCollapse = () => {
        const togglers = document.querySelectorAll('[toggler]');
        for (const toggler of togglers) {
            toggler.setAttribute('class', `collapse ${isCollapse ? 'show' : ''} `)
        }

        setIsCollapse(!isCollapse);
    }

    const renderSubList = (listChuanDauRa, pathFieldUpdated) => {
        return listChuanDauRa.length
            ? listChuanDauRa.map((chuanDauRa, chuanIndex) => {
                  if (!chuanDauRa?.children) {
                      return (
                          <li key={`${chuanIndex}`}>
                              <span>
                                  {props?.disableEdit ? (
                                      <Label>{`${chuanDauRa?.chuanDauRa?.ten}`}</Label>
                                  ) : (
                                      <Input
                                          onChange={(e) =>
                                              onHandEditItem(
                                                  e,
                                                  `${pathFieldUpdated}[${chuanIndex}].chuanDauRa.ten`
                                              )
                                          }
                                          value={`${chuanDauRa?.chuanDauRa?.ten}`}
                                      />
                                  )}
                                  {/* <FaRegCaretSquareUp 
                                    size="2em" 
                                    style={{ marginLeft: "10px", cursor: "pointer" }}
                                    /> */}
                                  {isModeEdit && (
                                      <>
                                      
                                        <FaPlusCircle 
                                            color="#10ac84" 
                                            style={{ marginLeft: "10px", cursor: "pointer" }}
                                            onClick={() =>
                                                handleAddItem(
                                                    `${pathFieldUpdated}[${chuanIndex}].children`,
                                                    `${chuanDauRa.ma || ""}`
                                                )
                                            }
                                            size="2em"
                                            />
                                            <FaTimesCircle 
                                                size="2em"
                                                color="#ee5253"
                                                style={{ marginLeft: "10px", cursor: "pointer" }}
                                                onClick={() =>
                                                    handleDeleteItem(
                                                        `${pathFieldUpdated}`,
                                                        `${chuanIndex}`
                                                    )
                                                }
                                            />
                                          {/* <Fab
                                              size="small"
                                              color="primary"
                                              style={{ marginLeft: "10px" }}
                                              onClick={() =>
                                                  handleAddItem(
                                                      `${pathFieldUpdated}[${chuanIndex}].children`,
                                                      `${chuanDauRa.ma || ""}`
                                                  )
                                              }
                                          >
                                              <AddIcon />
                                          </Fab>
                                          <Fab
                                              size="small"
                                              color="secondary"
                                              style={{ marginLeft: "10px" }}
                                              onClick={() =>
                                                  handleDeleteItem(
                                                      `${pathFieldUpdated}`,
                                                      `${chuanIndex}`
                                                  )
                                              }
                                          >
                                              <DeleteIcon />
                                          </Fab> */}
                                      </>
                                  )}
                              </span>
                          </li>
                      );
                  }
                  return (
                      <li  key={`${chuanIndex}`}>
                          <span className="toggler_parent">
                              {props?.disableEdit ? (
                                  <Label>{`${chuanDauRa?.chuanDauRa?.ten}`}</Label>
                              ) : (
                                  <Input
                                      onChange={(e) =>
                                          onHandEditItem(
                                              e,
                                              `${pathFieldUpdated}[${chuanIndex}].chuanDauRa.ten`
                                          )
                                      }
                                      value={`${chuanDauRa?.chuanDauRa?.ten}`}
                                  />
                              )}
                              <FaIndent 
                                size="2em" 
                                style={{ marginLeft: "15px", cursor: "pointer" }}
                                id={`toggler-${chuanDauRa?.chuanDauRa?.id}`}
                                />
                              {isModeEdit && (
                                  <>
                                  <FaPlusCircle 
                                    color="#10ac84" 
                                    style={{ marginLeft: "10px", cursor: "pointer" }}
                                    onClick={() =>
                                        handleAddItem(
                                            `${pathFieldUpdated}[${chuanIndex}].children`,
                                            `${chuanDauRa.ma || ""}`
                                        )
                                    }
                                    size="2em"
                                    />
                                    <FaTimesCircle 
                                        size="2em"
                                        color="#ee5253"
                                        style={{ marginLeft: "10px", cursor: "pointer" }}
                                        onClick={() =>
                                            handleDeleteItem(
                                                `${pathFieldUpdated}`,
                                                `${chuanIndex}`
                                            )
                                        }
                                    />
                                  </>
                              )}
                          </span>

                          <ol>
                            <UncontrolledCollapse toggler={`#toggler-${chuanDauRa?.chuanDauRa?.id}`}>
                              {renderSubList(
                                  chuanDauRa.children,
                                  `${pathFieldUpdated}[${chuanIndex}].children`
                              )}
                              </UncontrolledCollapse>
                          </ol>
                      </li>
                  );
              })
            : "";
    };

    return (
        <div>
            <div className="chuandaura_top_btn">
                <Button color="info" onClick={() => setIsModeEdit(!isModeEdit)}>
                    {isModeEdit
                        ? <FaEye />
                        : <FaEdit />}
                </Button>
                {/* <Button color="warning" onClick={props.handleSave}>
                    Lu Tt C
                </Button> */}
                <Button color="success" onClick={props.handleSubmit}>
                    {i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_&_SUBMIT')}
                </Button>
                <Button onClick={togglerCollapse}>
                    {i18n.t('FLOW_CREATE_CTDT_DETAIL.CHUAN_DAU_RA.SHOW_HIDDEN_BTN')}
                </Button>
            </div>
            <div className="chuandaura_content">
                {data && <ol>{renderSubList(data, "data")}</ol>}
            </div>
            {isModeEdit && (
                <div>
                    <FaPlusCircle 
                        color="#10ac84" 
                        style={{ marginLeft: "10px", cursor: "pointer" }}
                        onClick={() => handleAddItem("data")}
                        size="2em"
                    />
                    {/* <Fab
                        size="small"
                        color="primary"
                        style={{ marginLeft: "10px" }}
                        onClick={() => handleAddItem("data")}
                    >
                        <AddIcon />
                    </Fab> */}
                </div>
            )}
            <ModalAddNewOutputStandard
                modal={isOpenModalAdd}
                toggle={() => setIsOpenModalAdd(!isOpenModalAdd)}
                headerTitle={""}
                options={props.optionsDefaultOutputStandard}
                handleOkButtonOnClick={confirmHandleAddItem}
                handleCancelButtonOnClick={handleCloseAddStandardOutput}
            />
        </div>
    );
};

export default ChuanDauRaList;

const ModalAddNewOutputStandard = (props) => {
    const [optionSelected, setOptionSelected] = useState(null);

    useEffect(() => {
        setOptionSelected(null);
    }, []);

    const onChange = (selected) => {
        setOptionSelected(selected);
    };

    const loadOptions = async (inputValue, callback) => {
        const results = await standardOutput.getAll({
            search: inputValue,
        });
        let formatResults = [];
        if (results?.contents) {
            formatResults = results?.contents?.map((item) => {
                return {
                    label: `${item?.ten}` || "",
                    value: item?.id,
                    data: item,
                };
            });
        }
        callback(formatResults);
    };

    const handleOK = () => {
        if (optionSelected?.data) {
            const formatData = { ma: "", chuanDauRa: optionSelected.data };
            props.handleOkButtonOnClick(formatData);
            setOptionSelected(null);
        }
        props.toggle();
    };

    const handleCancel = () => {
        setOptionSelected(null);
        props.handleCancelButtonOnClick();
    };

    const { t } = useTranslation();

    return (
        <Modal isOpen={props.modal} toggle={props.toggle}>
            <ModalHeader>{props.headerTitle}</ModalHeader>
            <ModalBody>
                <div>
                    <Label>{i18n.t('FLOW_CREATE_CTDT_DETAIL.CHUAN_DAU_RA.CHUAN_DAU_RA_TITLE')}</Label>
                    <AsyncSelect
                        cacheOptions
                        defaultOptions={props.options}
                        loadOptions={loadOptions}
                        onChange={onChange}
                        // value={props.value}
                        // onInputChange={(newValue) => setSearch(newValue)}
                    />
                </div>
            </ModalBody>
            <ModalFooter>
                <Button color={`primary`} onClick={handleOK}>
                    {t(props.titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancel}>
                    {t(props.titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
};
