import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Button } from "reactstrap";
import Styles from "../component/styled";
import cloneTemplateAPI from "api/common/cloneTemplate.api";
import ConfirmSubmit from "../../../../components/admin/ConfirmModal/ConfirmModal";
import TablesSelection from "../component/TablesSelection";

import "./CauTrucChuongTrinh.css";
import Loading from "components/common/loading/Loading";
import TablesInputSelection from "../component/TablesInputSelection";

const emptyKhoiKienThuc = (chiTietNganh) => {
    return {
        // id: 1,
        chiTietNganh: chiTietNganh,
        maKKT: "",
        ten: "",
        tinChiTuChon: 0,
        tinChiBatBuoc: 0,
        tinChiTuChonTuDo: 0,
        ghiChu: "",
        tongTinChi: 0,
        loaiKhoiKienThuc: [],
    };
};

const khoiKienThucEnum = [
    {
        label: "Gio dc i cng",
        value: "Gio dc i cng"
    },
    {
        label: "C s ngnh",
        value: "C s ngnh"
    },
    {
        label: "Ngnh",
        value: "Ngnh"
    },
    {
        label: "Tt nghip",
        value: "Tt nghip"
    }
]

const CauTrucChuongTrinh = (props) => {
    const columns = React.useMemo(
        () => [
            {
                Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.BLOCK_KNOWLEDGE')}*`,
                accessor: "ten",
                width: 300,
                isInputSelect: true,
                options: khoiKienThucEnum
            },
            {
                Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.CREDIT_NUMBER')}`,
                columns: [
                    {
                        Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.LOAI_HP.BB')}`,
                        accessor: "tinChiBatBuoc",
                        width: 120,
                        isValidate: true,
                    },
                    {
                        Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.LOAI_HP.TC')}`,
                        accessor: "tinChiTuChon",
                        width: 120,
                        isValidate: true,
                    },
                    {
                        Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.LOAI_HP.TCTD')}`,
                        accessor: "tinChiTuChonTuDo",
                        width: 120,
                        isValidate: true,
                    },
                    {
                        Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.CREDIT_TOTAL')}`,
                        accessor: "tongTinChi",
                        disabled: true,
                        width: 120,
                        isValidate: true,
                    },
                ],
            },
            {
                Header: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.NOTE')}`,
                accessor: "ghiChu",
                width: 200,
            },
        ],
        []
    );
    const [total, setTotal] = useState(0);
    const [dataContainer, setDataContainer] = useState(null);
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isOpenModal, setIsOpenModal] = useState(false);
    const [isOpenDeleteModel, setIsOpenDeleteModel] = useState(false);
    const [selectedRowIndex, setSelectedRowIndex] = useState([]);

    //* get data from step state or from API
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const stateData = props.getState("dataClone", { khoiKienThuc: [] });
            if (stateData?.khoiKienThuc) {
                setDataContainer(stateData);
                setData(stateData?.khoiKienThuc);
                setIsLoading(false);
                return;
            }
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.ERROR_FETCH_DATA'));

            // const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
            // const { idCTNDTClone } = props.getState("step2", {
            //     idCTNDTClone: "",
            // });
            // if (idCTNDT && idCTNDTClone) {
            //     const data = await fetchTemplate({ idCTNDT, idCTNDTClone });
            //     setData(data);
            // } else {
            //     toast.error("Li, idCTNDT hoc idCTNDTClone rng");
            // }
            setIsLoading(false);
        })();
    }, []);

    //* calculator total tong so tin chi
    useEffect(() => {
        const tongSoTinChi = data.reduce((total, khoiKienThuc) => {
            return (total += Number(khoiKienThuc.tongTinChi));
        }, 0);
        setTotal(tongSoTinChi);
    }, [data]);

    //* call API get data clone noi dung chuong trinh
    // const fetchTemplate = async (params) => {
    //     try {
    //         const result = await cloneTemplateAPI.getTemplateKKT(params);
    //         return result;
    //     } catch (error) {
    //         toast.error("ERROR");
    //         return [];
    //     }
    // };

    //* add new a khoi kien thuc
    const addNewRow = () => {
        const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
        const cloneData = [...data];
        cloneData.push(emptyKhoiKienThuc(idCTNDT));
        setData(cloneData);
    };

    //* edit data and store to data state
    const updateMyData = (rowIndex, columnId, value) => {
        const newData = data.map((row, index) => {
            if (index === rowIndex) {
                if (columnId !== "ghiChu" && columnId !== "ten") {
                    data[rowIndex].tongTinChi =
                        Number(data[rowIndex].tinChiBatBuoc) +
                        Number(data[rowIndex].tinChiTuChon) +
                        Number(data[rowIndex].tinChiTuChonTuDo) +
                        Number(value) -
                        Number(data[rowIndex][columnId]);
                    return {
                        ...data[rowIndex],
                        [columnId]: value || 0,
                    };
                }
                return {
                    ...data[rowIndex],
                    [columnId]: value || "",
                };
            }
            return row;
        });

        setData(newData);
    };

    //* save data into state
    // const handleSaveData = () => {
    //     try {
    //         props.setState("dataClone", { ...dataContainer, khoiKienThuc: data });
    //         // props.setState("cauTrucChuongTrinh", data);
    //         toast.success("SUCCESS");
    //     } catch (error) {
    //         toast.error("Lu li");
    //         return;
    //     }
    // };

    //* open modal confirm submit
    const openConfirmSubmit = () => {
        setIsOpenModal(true);
    };

    //* call API to create new data
    const handleSubmit = () => {
        try {
            const isNotNull = checkKKTNotNull(data);
            if(!isNotNull){
                toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TEN_KKT_NOT_NULL'))
                return;
            }
            props.setState("dataClone", { ...dataContainer, khoiKienThuc: data });
            // props.setState("cauTrucChuongTrinh", data);
            // toast.success(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_SUCCESS'));
            props.next();
        } catch (error) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_ERROR'));
            return;
        }finally{
            setIsOpenModal(false);
        }
    };

    const checkKKTNotNull = (data) => {
        if(!data?.length){
            return false;
        }
        for (const kkt of data) {
            // eslint-disable-next-line 
            if(kkt?.ten == null || kkt?.ten === ''){
                return false
            }
        }
        return true;
    }

    //* selected rows to delete
    const handleSelectedRow = (listSelectedRowId) => {
        const converListSelectedRowId = listSelectedRowId.map((id) =>
            Number(id)
        );
        setSelectedRowIndex(converListSelectedRowId);
    };

    //* open modal confirm delete khoi kien thuc
    const openConfirmDelete = () => {
        setIsOpenDeleteModel(true);
    };

    //* handle deleted khoi kien thuc
    const deleteRows = () => {
        const newData = data.filter((row, index) => {
            if (selectedRowIndex.includes(index)) {
                return false;
            }
            return true;
        });
        setData(newData);
        setIsOpenDeleteModel(false);
    };

    //* contents modal confirm submit
    const bodyConfirmModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    //* contents modal confirm delete
    const bodyConfirmDeleteModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    return (
        <div>
            <div className="cautruc_btn_header">
                <h2>{i18n.t("STRUCTURE_BASIC")}</h2>
            </div>

            {isLoading ? (
                <Loading />
            ) : (
                <Styles>
                    <div className="cautruc_btn_top">
                        <Button onClick={openConfirmDelete} color="danger">
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.DELETE')}
                        </Button>
                        <Button
                            onClick={addNewRow}
                            color="info"
                            className="cautruc_btn add_new_row_btn"
                        >
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.ADD')}
                        </Button>
                        {/* <Button onClick={handleSaveData} color="warning">
                            Lu
                        </Button> */}
                        <Button
                            className="cautruc_btn submit_btn"
                            onClick={openConfirmSubmit}
                            color="success"
                        >
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_&_SUBMIT')}
                        </Button>
                    </div>

                    <TablesInputSelection
                        columns={columns}
                        data={data}
                        updateMyData={updateMyData}
                        isPagination={false}
                        handleSelectedRow={handleSelectedRow}
                        tongSoTinChi={total}
                        isFooterSum={true}
                    />
                </Styles>
            )}
            <ConfirmSubmit
                modal={isOpenModal}
                toggle={() => setIsOpenModal(!isOpenModal)}
                headerTitle={""}
                bodyContent={bodyConfirmModal()}
                handleOkButtonOnClick={handleSubmit}
                handleCancelButtonOnClick={() => setIsOpenModal(!isOpenModal)}
            />
            <ConfirmSubmit
                modal={isOpenDeleteModel}
                toggle={() => setIsOpenDeleteModel(!isOpenDeleteModel)}
                headerTitle={""}
                bodyContent={bodyConfirmDeleteModal()}
                handleOkButtonOnClick={deleteRows}
                handleCancelButtonOnClick={() =>
                    setIsOpenDeleteModel(!isOpenDeleteModel)
                }
                isModalDelete={true}
            />
        </div>
    );
};

export default CauTrucChuongTrinh;
