import cloneTemplateApi from "api/common/cloneTemplate.api";
import axios from "axios";
import Loading from "components/common/loading/Loading";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import CONFIG from "config/config";
import { RoutesString } from "pages/routesString";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { toast } from "react-toastify";
import { Button } from "reactstrap";
import i18n from "i18n/i18n";
import './DonePage.css';

const DonePage = (props) => {
    const history = useHistory();
    const [isProcessing, setIsProcessing] = useState(false);
    const [isLoading, setIsLoading] = useState(false)
    const [filePdf, setFilePdf] = useState(null);

    const onCreateDataClone = async () => {
        const stateData = props.getState("dataClone");
        if (stateData && stateData?.step1_state?.idCTNDT) {
            const { step1_state, chuanDauRa, keHoachGiangDay, khoiKienThuc, ...rest } = stateData;
            const params = {
                idCTNDT: step1_state?.idCTNDT,
            }
            const payload = {
                chuanDauRa,
                keHoachGiangDay,
                khoiKienThuc
            }
            setIsLoading(true);
            try {
                const results = await cloneTemplateApi.postCloneDataV2(params, payload)
                toast.info(i18n.t('FLOW_CREATE_CTDT_DETAIL.DONE.CREATE_SUCCESS'));
                setTimeout(() => {
                    history.push(RoutesString.ADMIN_VIEW_CHI_TIET_NGANH_DAO_TAO);
                }, 2000)
            } catch (error) {
                toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.DONE.CREATE_ERROR'));
            }finally{
                setIsLoading(false);
            }
        } else {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.DONE.CREATE_ERROR'));
        }
    }

    const onHandlePreviewPdf = async () => {
        const stateData = props.getState("dataClone");
        if (stateData && stateData?.step1_state?.idCTNDT) {
            setIsProcessing(true);
            const { step1_state, chuanDauRa, keHoachGiangDay, khoiKienThuc, ...rest } = stateData;

            const payload = {
                khoiKienThuc,
                chuanDauRaNganhDaoTao: chuanDauRa,
                cauTrucChuongTrinh: khoiKienThuc,
                keHoachGiangDay,
            }
            axios({
                url: `${CONFIG.API.EXPORT_PDF}/preview/pdf/${step1_state?.idCTNDT}`,
                method: "POST",
                responseType: "blob", // important
                data: {
                    data: JSON.stringify(payload)
                }
            })
                .then((response) => {
                    //Create a Blob from the PDF Stream
                    const file = new Blob([response.data], {
                        type: "application/pdf",
                    });
                    //Build a URL from the file
                    const fileURL = URL.createObjectURL(file);
                    //Open the URL on new Window
                    // window.open(fileURL);
                    setFilePdf(fileURL);
                    // setIsProcessing(false);
                })
                .catch((error) => {
                    // eslint-disable-next-line
                    console.log(error);
                    toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.DONE.RENDER_ERROR'))
                }).finally(() => {
                    setIsProcessing(false);
                })
        }
    };
    return (
        <div >
            {isLoading ? <LoadingBar /> : ''}
            <div>
                <Button onClick={onCreateDataClone} style={{ marginLeft: "10px" }} color='success'>{i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_&_SUBMIT')}</Button>
                <Button onClick={onHandlePreviewPdf} color="primary" style={{ marginLeft: "10px" }}>
                    {i18n.t('FLOW_CREATE_CTDT_DETAIL.PREVIEW')}
                </Button>
            </div>
            <div style={{ margin: "10px auto", textAlign: "center" }}>
                {isProcessing ? (
                    <div className="process_container">
                        <div class="dots-3"></div>
                    </div>
                ) : (
                    <div >
                        {filePdf && (
                            <iframe
                                style={{display: "flex", margin: "0 auto"}}
                                src={filePdf}
                                height={"650"}
                                width={"1000"}
                            />
                        )}
                    </div>
                )}
            </div>
        </div>
    );
};

export default DonePage;
