import styled from "styled-components";
const Styles = styled.div`
    .table {
        display: inline-block;
        border-spacing: 0;
        border: 1px solid black;
        width: fit-content;
        margin-top: 10px;
        .tr {
            :last-child {
                .td {
                    border-bottom: 0 !important;
                }
            }
        }
        .th {
            font-weight: 700;
        }
        .th,
        .td {
            margin: 0;
            padding: 0.5rem;
            border-bottom: 1px solid black;
            border-right: 1px solid black;

            :last-child {
                border-right: 0;
            }

            input {
                font-size: 1rem;
                padding: 0;
                margin: 0;
                border: 0;
                width: 100%;
            }
        }
    }

    .pagination {
        padding: 0.5rem;
    }
`;
export default Styles;
