import StepsForm from "components/common/StepProgressBar/StepProgressBar";
import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
import { Row, Col, Button } from "reactstrap";
const Navigation = (props) => {
    const steps = React.useMemo(() => [
        {
            label: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_STATUS_BAR.CHOOSE_MAJOR')}`,
            name: 'step1',
            order: 1
        },
        // {
        //     label: 'Chn ngnh o to sao chp',
        //     name: 'step2',
        //     order: 2
        // },
        {
            label: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_STATUS_BAR.STANDARD_OUTPUT')}`,
            name: 'step2',
            order: 2
        },
        {
            label: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_STATUS_BAR.STRUCT_PROGRAM')}`,
            name: 'step3',
            order: 3
        },
        {
            label: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_STATUS_BAR.CONTENT_PROGRAM')}`,
            name: 'step4',
            order: 4
        },
        {
            label: `${i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_STATUS_BAR.PLAN_PRAGRAM')}`,
            name: 'step5',
            order: 5
        },
        {
            label: 'Preview',
            name: 'step6',
            order: 6
        }
    ], []);
    return (
        <div
            className="nav_row"
            style={{
                // display: "flex",
                // justifyContent: "space-between",
                // padding: "5px 0px 5px 10px",
            }}
        >
            <div>
                <StepsForm steps={steps} activeStep={props?.current} />
            </div>

            <div style={{ display: 'flex', justifyContent: 'space-between', margin: '10px 0' }}>

                <Button onClick={props.prev} disabled={props?.current === 1}>&laquo; {i18n.t("PREVIOUS")}</Button>

                <Button onClick={props.next} disabled={props?.current === props.size} style={{ marginRight: '10px' }}>{i18n.t("NEXT")} &raquo;</Button>

            </div>
            {/* <div className="nav_col">
                {props.current !== 1 && (
                    <Button
                        type="primary"
                        onClick={props.prev}
                        style={{ marginRight: 10 }}
                        disabled={props.current === 1}
                    >
                        Previous
                    </Button>
                )}
            </div> */}
            {/* {props.current === 1 ? null : (
                <div className="nav_col">
                    <Button
                        type="primary"
                        onClick={props.next}
                        disabled={props.current === props.size}
                        onClick={onClickNextStep}
                    >
                        Submit
                    </Button>
                </div>
            )} */}
        </div>
    );
};

export default Navigation;
