import React, { useEffect, useState } from "react";
// import cloneTemplateAPI from "api/common/cloneTemplate.api";
import Loading from "components/common/loading/Loading";
// import Styles from "../component/styled";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";
import ChuanDauRaList from "components/common/ChuanDauRaList/ChuanDauRaList";
import standardOutput from "api/common/standardOutput";
// import educationMajorOutputStandard from "api/common/education-major-output-standard";
import ConfirmModal from "components/admin/ConfirmModal/ConfirmModal";

const ChuanDauRa = (props) => {
    const [dataContainer, setDataContainer] = useState(null);
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isOpenConfirmSubmit, setIsOpenConfirmSubmit] = useState(false);
    const [optionsOutputStandard, setOptionsOutputStandard] = useState([]);

    //* Get default chuan dau ra
    useEffect(() => {
        (async () => {
            const results = await standardOutput.getAll({
                limit: 10,
            });
            if (results?.contents) {
                const formatResults = results?.contents?.map((item) => {
                    return {
                        label: `${item?.ten}` || "",
                        value: item?.id,
                        data: item,
                    };
                });
                setOptionsOutputStandard(formatResults);
            }
        })();
    }, []);

    //* Call get clone chuan dau ra
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const stateData = props.getState("dataClone", { chuanDauRa: [] });
            if (stateData?.chuanDauRa) {
                setDataContainer(stateData)
                setData(stateData?.chuanDauRa);
                setIsLoading(false);
                return;
            }
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.ERROR_FETCH_DATA'));

            // const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
            // const { idCTNDTClone } = props.getState("step2", {
            //     idCTNDTClone: "",
            // });
            // if (idCTNDT && idCTNDTClone) {
            //     const data = await fetchTemplateChuanDauRa({
            //         idCTNDT,
            //         idCTNDTClone,
            //     });
            //     setData(data);
            // } else {
            //     toast.error("ERROR, idCTNDT or idCTNDTClone empty");
            // }
            setIsLoading(false);
        })();
    }, []);

    //* call API get clone chuan dau ra
    // const fetchTemplateChuanDauRa = async (params) => {
    //     try {
    //         const result = await cloneTemplateAPI.getChuanDauRa(params);
    //         return result;
    //     } catch (error) {
    //         toast.error("ERROR");
    //         return [];
    //     }
    // };

    //* save to data
    // const handleSave = () => {
    //     props.setState("dataClone", {...dataContainer, chuanDauRa: data});
    //     toast.success("SUCCESS");
    // };

    //* save & submit data to server
    const handleSubmit = async () => {
        try {
            props.setState("dataClone", { ...dataContainer, chuanDauRa: data });
            // await handleSubmitTemplateChuanDauRa(data);
            // toast.success(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_SUCCESS'));
            props.next();
        } catch (error) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.SUBMIT_ERROR'));
            return;
        } finally {
            setIsOpenConfirmSubmit(false);
        }
    };

    //* call API to create chuan dau ra
    // const handleSubmitTemplateChuanDauRa = async (data) => {
    //     const { idCTNDT } = props.getState("step1", { idCTNDT: "" });
    //     const { idCTNDTClone } = props.getState("step2", {
    //         idCTNDTClone: "",
    //     });
    //     if (idCTNDT && idCTNDTClone) {
    //         const result = await cloneTemplateAPI.postChuanDauRa(
    //             { idCTNDT, idCTNDTClone },
    //             data
    //         );
    //         return result;
    //         // eslint-disable-next-line
    //     } else {
    //         toast.error("ERROR, idCTNDT or idCTNDTClone empty");
    //     }
    // };

    //* pass to child component to update data
    const handleSetData = (data) => {
        setData(data);
    };

    //* open modal confirm submit
    const openConfirmSubmit = () => {
        setIsOpenConfirmSubmit(true);
    };

    //* contents of confirm modal
    const bodyConfirmModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    return (
        <div>
            <div>
                <h2>{i18n.t('FLOW_CREATE_CTDT_DETAIL.CHUAN_DAU_RA.CHUAN_DAU_RA_TITLE')}</h2>
            </div>

            {isLoading ? (
                <Loading />
            ) : (
                <div>
                    <ChuanDauRaList
                        values={data}
                        // handleSave={handleSave}
                        handleSubmit={openConfirmSubmit}
                        handleSetData={handleSetData}
                        optionsDefaultOutputStandard={optionsOutputStandard}
                        disableEdit={true}
                    />
                </div>
            )}
            <ConfirmModal
                modal={isOpenConfirmSubmit}
                toggle={() => setIsOpenConfirmSubmit(!isOpenConfirmSubmit)}
                headerTitle={""}
                bodyContent={bodyConfirmModal()}
                handleOkButtonOnClick={handleSubmit}
                handleCancelButtonOnClick={() =>
                    setIsOpenConfirmSubmit(!isOpenConfirmSubmit)
                }
            />
        </div>
    );
};

export default ChuanDauRa;
