//     return data.map((item) => {
//         return { label: item.monHoc.tenTiengViet, value: item, id: item.id };
//     });
// };

const transformData = (data) => {
    return data.map((item) => {
        return { label: item.tenHocKy, value: item, tenHocKy: item.tenHocKy };
    });
};

const extractSubjects = data => {
    let list = [];
    for (const kkt of data) {
        for (const lkkt of kkt?.loaiKhoiKienThuc) {
            for (const gn of lkkt?.gomNhom) {
                for (const ctgn of gn?.chiTietGomNhom) {
                    list.push(ctgn?.monHoc);
                }
            }
        }
    }
    return list;
}

