const ModalAddNewSubject = (props) => {
    const [optionSelected, setOptionSelected] = useState(null);
    const [search, setSearch] = useState("");
    const [hocKy, setHocKy] = useState(null);
    const [subject, setSubject] = useState(null);

    const onChange = (selected) => {
        setOptionSelected(selected);
    };

    const handleOk = () => {
        if (optionSelected?.data) {
            props.handleOkButtonOnClick(hocKy, optionSelected.data);
            setOptionSelected(null);
        }
        props.toggle();
    };

    const handleCancel = () => {
        setHocKy(null);
        setSubject(null);
        props.toggle();
    };

    return (
        <Modal isOpen={props.modal} toggle={props.toggle}>
            <ModalHeader></ModalHeader>
            <ModalBody>
                <div>
                    <div>
                        <Label>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.KE_HOACH.SEMESTER_NAME')}: `}</Label>
                        <InputSelectSearchable
                            options={props.optionsHocKy}
                            onChange={(selected) => setHocKy(selected)}
                        />
                    </div>
                    <div>
                        <Label>{`${i18n.t('FLOW_CREATE_CTDT_DETAIL.KE_HOACH.SUBJECT')}: `}</Label>
                        <InputSelectSearchable
                            options={props.optionsListSubjects}
                            onChange={onChange}
                            // onChange={(selected) => setHocKy(selected)}
                            // cacheOptions
                            // defaultOptions={[]}
                            // loadOptions={loadOptions}
                            // value={optionSelected}
                            // onInputChange={(newValue) => setSearch(newValue)}
                        />
                    </div>
                </div>
            </ModalBody>
            <ModalFooter>
                <Button color={`primary`} onClick={handleOk}>
                    {i18n.t(props.titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancel}>
                    {i18n.t(props.titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
};

