import React, { useEffect, useState } from "react";
import { Form, FormGroup, Label, Button, Input } from "reactstrap";
import InputSelectSearchable from "components/common/InputSelectSearchable/InputSelectSearchable";
import chuongTrinhDaoTaoService from "api/common/chuong-trinh-dao-tao.api";
import nganhDaoTaoService from "api/common/trainingMajor";
import chiTietNganhDaoTaoService from "api/common/subMajor";
import i18n from "i18n/i18n";
import { toast } from "react-toastify";
import cloneTemplateApi from "api/common/cloneTemplate.api";

import "./Step1_Wrapper.css";
const Step1_Init = (props) => {
    const [listChuongTrinhDaoTao, setListChuongTrinhDaoTao] = useState([]);
    const [listNganhDaoTao, setListNganhDaoTao] = useState([]);
    const [listKhoaTuyen, setListKhoaTuyen] = useState([]);
    const [listIdCTNDTClone, setListIdCTNDTClone] = useState([]);

    const dataClone = props.getState("dataClone");
    const [chuongTrinhDaoTaoSelected, setChuongTrinhDaoTaoSelected] = useState(
        dataClone?.step1_state?.chuongTrinhDaoTaoSelected || null
    );
    const [nganhDaoTaoSelected, setNganhDaoTaoSelected] = useState(
        dataClone?.step1_state?.nganhDaoTaoSelected || null
    );
    const [khoaTuyen, setKhoaTuyen] = useState(dataClone?.step1_state?.khoaTuyen);
    const [chiTietNganhDaoTao, setChiTietNganhDaoTao] = useState(
        dataClone?.step1_state?.idCTNDT || null
    );
    const [chiTietNganhDaoTaoClone, setChiTietNganhDaoTaoClone] = useState(dataClone?.step1_state?.chiTietNganhDaoTaoClone || null);
    const [idCTNDTClone, setIdCTNDTClone] = useState(dataClone?.step1_state?.idCTNDTClone || null);

    const [modeCreate, setModeCreate] = useState(typeof dataClone?.step1_state?.modeCreate === 'undefined' ? true : dataClone?.step1_state?.modeCreate);

    //* Get list chuong trinh dao tao API
    useEffect(() => {
        (async () => {
            const results = await chuongTrinhDaoTaoService.getAll({});
            const chuongTrinhDaoTao =
                results?.contents.map((item) => {
                    return { value: item.maCTDT, label: item.ten, id: item.id };
                }) || [];
            setListChuongTrinhDaoTao(chuongTrinhDaoTao);
            await getChiTietNganhDaoTaoClone();
        })();
    }, []);

    //* Then, Get list nganh dao tao API
    useEffect(() => {
        (async () => {
            if (chuongTrinhDaoTaoSelected?.id) {
                await getNganhDaoTao(chuongTrinhDaoTaoSelected.id);
            }
        })();
    }, [chuongTrinhDaoTaoSelected?.value]);

    //* Final, Get list chi tiet nganh dao tao API
    useEffect(() => {
        (async () => {
            if (nganhDaoTaoSelected?.id) {
                await getChiTietNganhDaoTao(nganhDaoTaoSelected?.id);
            }
        })();
    }, [nganhDaoTaoSelected?.value]);

    const getNganhDaoTao = async (id) => {
        try {
            const results = await nganhDaoTaoService.getWithFilter({
                chuongTrinhDaoTao: id,
            });
            let nganhDaoTao = [];
            if (results?.contents?.length) {
                nganhDaoTao =
                    results?.contents?.map((item) => {
                        return {
                            value: item.maNganhDaoTao,
                            label: item.ten,
                            id: item?.id,
                        };
                    }) || [];
            }
            setListNganhDaoTao(nganhDaoTao);
        } catch (error) {
            setListNganhDaoTao([]);
        }
    };

    const getChiTietNganhDaoTao = async (id) => {
        try {
            const results = await chiTietNganhDaoTaoService.getWithFilter({
                nganhDaoTao: id,
            });
            let chiTietNDT = [];
            if (results?.contents?.length) {
                chiTietNDT =
                    results?.contents?.map((item) => {
                        return {
                            value: item.khoa,
                            label: item.khoa,
                            id: item?.id,
                        };
                    }) || [];
            }
            setListKhoaTuyen(chiTietNDT);
        } catch (error) {
            setListKhoaTuyen([]);
        }
    };

    const getChiTietNganhDaoTaoClone = async (id) => {
        try {
            const results = await chiTietNganhDaoTaoService.getAll();

            let chiTietNDTClone = [];
            if (results?.contents?.length) {
                chiTietNDTClone =
                    results?.contents?.map((item) => {
                        return {
                            value: item,
                            label: `${item?.nganhDaoTao?.chuongTrinhDaoTao?.ten} - ${item?.nganhDaoTao?.ten} - ${item.khoa}`,
                            id: item?.id,
                        };
                    }) || [];
            }
            setListIdCTNDTClone(chiTietNDTClone);
        } catch (error) {
            setListIdCTNDTClone([]);
        }
    };

    const handleChangeChuongTrinhDaoTao = (selectedOption) => {
        setChuongTrinhDaoTaoSelected(selectedOption);
        setNganhDaoTaoSelected(null);
        setKhoaTuyen(null);
    };

    const handleChangeNganhDaoTao = (selectedOption) => {
        setNganhDaoTaoSelected(selectedOption);
        setKhoaTuyen(null);
    };

    const handleChangeKhoaTuyen = (selectedOption) => {
        if (selectedOption?.id) {
            setChiTietNganhDaoTao(selectedOption?.id);
        }
        setKhoaTuyen(selectedOption);
    };

    const handChangeIdCTNDTClone = (selectedOption) => {
        if (selectedOption?.id) {
            setIdCTNDTClone(selectedOption?.id)
        }
        setChiTietNganhDaoTaoClone(selectedOption);
    }

    const handleOnSubmit = async () => {
        if (
            !chiTietNganhDaoTao ||
            !chuongTrinhDaoTaoSelected ||
            !nganhDaoTaoSelected ||
            !khoaTuyen
        ) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_1.WARNING_INFO_FULL'));
            return;
        }

        if ((!modeCreate && !idCTNDTClone) || !chiTietNganhDaoTao) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_1.ERROR_INFO_MISSING'));
            return;
        }

        if (!modeCreate && idCTNDTClone === chiTietNganhDaoTao) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_1.ERROR_ID_DUPLICATE'));
            return;
        }

        const isContentExist = await cloneTemplateApi.checkContentExist({ idCTNDT: chiTietNganhDaoTao });
        if (isContentExist) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.STEP_1.ERROR_EXISTS_DATA'));
            return;
        }

        // const stateData = props.getState("dataClone");
        let results;

        if (modeCreate) {
            results = {
                chuanDauRa: [],
                keHoachGiangDay: [],
                khoiKienThuc: []
            }
        } else {
            const resp = await getDataClone({ idCTNDT: chiTietNganhDaoTao, idCTNDTClone });
            if (!resp?.isError) {
                results = resp?.data;
            } else {
                toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.ERROR_FETCH_DATA'));
                return;
            }
        }

        const prepareData = {
            ...results,
            step1_state: {
                idCTNDT: chiTietNganhDaoTao,
                chuongTrinhDaoTaoSelected,
                nganhDaoTaoSelected,
                khoaTuyen,
                chiTietNganhDaoTaoClone,
                idCTNDTClone,
                modeCreate
            }
        }
        props.setState('dataClone', prepareData);
        props.next();
    };

    const getDataClone = async (params) => {
        return await cloneTemplateApi.getCloneDataV2(params);
    }

    return (
        <div className="basic_info_container">
            <h2 className="flow_create_ctdt_title">
                {i18n.t("FLOW_CREATE_CTDT.TITLE_IDCTDT")}
            </h2>
            <Form>
                <FormGroup className="basicinfo_row">
                    <div>
                        <Label className="basicinfo_label">
                            {i18n.t("FLOW_CREATE_CTDT.EDUCATION_PROGRAM")}
                        </Label>
                        <InputSelectSearchable
                            value={chuongTrinhDaoTaoSelected}
                            options={listChuongTrinhDaoTao}
                            onChange={handleChangeChuongTrinhDaoTao}
                            className="basicinfo_input"
                        />
                    </div>
                </FormGroup>
                <FormGroup className="basicinfo_row">
                    <Label className="basicinfo_label">
                        {i18n.t("FLOW_CREATE_CTDT.MAJORS")}
                    </Label>
                    <InputSelectSearchable
                        value={nganhDaoTaoSelected}
                        options={listNganhDaoTao}
                        onChange={handleChangeNganhDaoTao}
                    />
                </FormGroup>
                <FormGroup className="basicinfo_row">
                    <Label className="basicinfo_label">
                        {i18n.t("FLOW_CREATE_CTDT.SCHOOL_YEAR")}
                    </Label>
                    <InputSelectSearchable
                        value={khoaTuyen}
                        options={listKhoaTuyen}
                        onChange={handleChangeKhoaTuyen}
                    />
                </FormGroup>

                <FormGroup style={{ display: "flex", marginLeft: '20px' }}>
                    <div>
                        <Label>
                            <Input type="radio" name="modeCreate" checked={!modeCreate} onClick={() => setModeCreate(false)} />{' '}
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.CLONE')}
                        </Label>
                    </div>

                    <div style={{ marginLeft: '30px' }}>
                        <Label>
                            <Input type="radio" name="modeCreate" checked={modeCreate} onClick={() => setModeCreate(true)} />{' '}
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.NEW')}
                        </Label>
                    </div>
                </FormGroup>

                {!modeCreate && <div>
                    {/* <h2 className="flow_create_ctdt_title">
                        {i18n.t("FLOW_CREATE_CTDT.TITLE_IDCTDT_CLONE")}
                    </h2> */}

                    <FormGroup className="basicinfo_row">
                        <Label className="basicinfo_label">
                            {i18n.t("FLOW_CREATE_CTDT.TITLE_IDCTDT_CLONE")}
                        </Label>
                        <InputSelectSearchable
                            value={chiTietNganhDaoTaoClone}
                            options={listIdCTNDTClone}
                            onChange={handChangeIdCTNDTClone}
                        />
                    </FormGroup>
                </div>
                }

            </Form>
            <div className="basicinfo_row">
                <Button
                    className="btn_submit_step1"
                    color="success"
                    onClick={handleOnSubmit}
                >
                    {i18n.t("SUBMIT")}
                </Button>
            </div>
        </div>
    );
};

export default Step1_Init;
