import React, { useEffect, useState } from "react";
import CreateKhoiKienThuc from "../component/CreateNewKhoiKienThuc";
import { Modal, ModalHeader, ModalBody } from "reactstrap";

import "./css/ModalCreateLoaiKhoiKienThuc.css";

const ModalCreateLoaiKhoiKienThuc = (props) => {
    return (
        <Modal isOpen={props.isOpenCreateKKT} toggle={props.toggleCreateKKT}>
            <ModalHeader toggle={props.toggleCreateKKT}>

            </ModalHeader>
            <ModalBody>
                <CreateKhoiKienThuc
                    khoiKienThuc={props.khoiKienThuc}
                    submit={props.addNewKhoiKienThuc}
                    toggleCreateKKT={props.toggleCreateKKT}
                />
            </ModalBody>
        </Modal>
    );
};

export default ModalCreateLoaiKhoiKienThuc;
