import React, { useEffect, useState } from "react";
import {
    useTable,
    usePagination,
    useRowState,
    useBlockLayout,
    useRowSelect,
} from "react-table";

import EditableCell from "./EditableCell";
import SelectableCell from "./SelectableCell";
import InputSelect from './InputSelection';
import IndeterminateCheckbox from "./IndeterminateCheckbox";
import i18n from "i18n/i18n";

const defaultColumn = {
    Cell: EditableCell,
    Select: SelectableCell,
    InputSelect: InputSelect,
    width: 50,
};

function TablesInputSelection({
    columns,
    data,
    updateMyData,
    skipPageReset,
    isPagination,
    handleSelectedRow,
    tongSoTinChi = 0,
    isFooterSum = false
}) {
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        prepareRow,
        footerGroups,
        rows,
        state: { selectedRowIds, ...rest },
    } = useTable(
        {
            columns,
            data,
            defaultColumn,
            updateMyData,
        },
        usePagination,
        useRowState,
        useBlockLayout,
        useRowSelect,
        (hooks) => {
            hooks.visibleColumns.push((columns) => [
                {
                    id: "selection",
                    Header: ({ getToggleAllRowsSelectedProps }) => (
                        <div>
                            <IndeterminateCheckbox
                                {...getToggleAllRowsSelectedProps()}
                            />
                        </div>
                    ),
                    Cell: ({ row }) => (
                        <div>
                            <IndeterminateCheckbox
                                {...row.getToggleRowSelectedProps()}
                            />
                        </div>
                    ),
                },
                ...columns,
            ]);
        }
    );

    useEffect(() => {
        handleSelectedRow(Object.keys(selectedRowIds));
    }, [Object.keys(selectedRowIds).length]);

    return (
        <>
            <div {...getTableProps()} className="table">
                <div>
                    {headerGroups.map((headerGroup) => (
                        <div
                            {...headerGroup.getHeaderGroupProps()}
                            className="tr"
                        >
                            {headerGroup.headers.map((column) => (
                                <div
                                    {...column.getHeaderProps()}
                                    className="th"
                                >
                                    {column.render("Header")}
                                </div>
                            ))}
                        </div>
                    ))}
                </div>

                <div {...getTableBodyProps()}>
                    {rows.map((row, i) => {
                        prepareRow(row);
                        return (
                            <div {...row.getRowProps()} className="tr">
                                {row.cells.map((cell) => {
                                    let ComponentChild = () =>
                                        cell.render("Cell");
                                    if (cell?.column?.disabled) {
                                        ComponentChild = () =>
                                            cell.render(() => (
                                                <input
                                                    disabled
                                                    value={cell?.value}
                                                />
                                            ));
                                    } else if (cell?.column?.isSelect) {
                                        ComponentChild = () =>
                                            cell.render("Select", {
                                                options: cell?.column?.options,
                                                value: cell?.value,
                                            });
                                    } else if (cell?.column?.isValidate) {
                                        ComponentChild = () =>
                                            cell.render("Cell", {
                                                isValidate: true,
                                            });
                                    }
                                    else if (cell?.column?.isInputSelect) {
                                        ComponentChild = () =>
                                            cell.render("InputSelect", {
                                                options: cell?.column?.options,
                                                value: cell?.value,
                                            });
                                    }
                                    return (
                                        <div
                                            {...cell.getCellProps()}
                                            className="td"
                                        >
                                            <ComponentChild />
                                        </div>
                                    );
                                })}
                            </div>
                        );
                    })}
                    {isFooterSum && <div style={{ display: "flex", padding: "5px 0" }}>
                        <div style={{ width: "700px", textAlign: "right" }}>
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.CAU_TRUC_CHUONG_TRINH.TABLE_HEADERS.CREDIT_TOTAL')}
                        </div>
                        <div style={{ marginLeft: "20px" }}>{tongSoTinChi}</div>
                    </div>}
                </div>
            </div>
            {/* <p>Selected Rows: {Object.keys(selectedRowIds).length}</p>
            <pre>
                <code>
                    {JSON.stringify(
                        {
                            selectedRowIds: selectedRowIds,
                        },
                        null,
                        2
                    )}
                </code>
            </pre> */}
            {/* {isPagination ? (
                <div className="pagination">
                    <button
                        onClick={() => gotoPage(0)}
                        disabled={!canPreviousPage}
                    >
                        {"<<"}
                    </button>{" "}
                    <button
                        onClick={() => previousPage()}
                        disabled={!canPreviousPage}
                    >
                        {"<"}
                    </button>{" "}
                    <button onClick={() => nextPage()} disabled={!canNextPage}>
                        {">"}
                    </button>{" "}
                    <button
                        onClick={() => gotoPage(pageCount - 1)}
                        disabled={!canNextPage}
                    >
                        {">>"}
                    </button>{" "}
                    <span>
                        Page{" "}
                        <strong>
                            {pageIndex + 1} of {pageOptions.length}
                        </strong>{" "}
                    </span>
                    <span>
                        | Go to page:{" "}
                        <input
                            type="number"
                            defaultValue={pageIndex + 1}
                            onChange={(e) => {
                                const page = e.target.value
                                    ? Number(e.target.value) - 1
                                    : 0;
                                gotoPage(page);
                            }}
                            style={{ width: "100px" }}
                        />
                    </span>{" "}
                    <select
                        value={pageSize}
                        onChange={(e) => {
                            setPageSize(Number(e.target.value));
                        }}
                    >
                        {[10, 20, 30, 40, 50].map((pageSize) => (
                            <option key={pageSize} value={pageSize}>
                                Show {pageSize}
                            </option>
                        ))}
                    </select>
                </div>
            ) : (
                ""
            )} */}
        </>
    );
}

export default TablesInputSelection;
