import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";
import Styles from "../component/styled";
import TablesSelection from "../component/TablesSelection";
import * as lodash from "lodash";
import { toast } from "react-toastify";
// import subject from "api/common/subject";
import ConfirmSubmit from "../../../../components/admin/ConfirmModal/ConfirmModal";
import CreateNewSubject from "../component/CreateNewSubject";
import { transformData } from "../utils";
import i18n from "i18n/i18n";
import TablesSelectionNoiDung from "../component/TablesSelectionNoiDung";

const NoiDungChuongTrinh = ({
    columns,
    subjects,
    modifyData,
    defaultSubjects,
    modifyDataContainer,
    // addItemToGroup,
    // typeGroup,
    dataContainer,
    listGomNhom,
    moveToSubject
}) => {
    const [data, setData] = useState([]);
    const [selectedRowIndex, setSelectedRowIndex] = useState([]);
    const [isOpenDeleteModal, setIsOpenDeleteModal] = useState(false);
    const [isOpenAddSubjectModal, setIsOpenAddSubjectModal] = useState(false);

    //* set data into state
    useEffect(() => {
        setData(subjects);
    }, [subjects]);

    //* add new subject into state
    const addNewMonHoc = (newSubject) => {
        const isExist = checkSubjectIsExist(newSubject?.idMH);
        if(isExist){
            toast.error(i18n.t("FLOW_CREATE_CTDT_DETAIL.NOI_DUNG.SUBJECT_WAS_EXISTS"));
            return;
        }
        
        const cloneData = [...data];
        cloneData.push(newSubject);
        // addItemToGroup(newSubject, typeGroup);
        setData(cloneData);
    };

    //* update new value into state
    const updateMyData = async (rowIndex, columnId, value) => {
        const objLodash = { data: [...data] };
        let valueUpdate = value;
        if (
            !columnId.includes("ma") &&
            !columnId.includes("tenTiengViet") &&
            !columnId.includes("tenTiengAnh") && !columnId.includes("ghiChu")
        ) {
            valueUpdate = Number(value);
        }
        let newData = lodash.set(
            objLodash,
            `data[${rowIndex}].${columnId}`,
            valueUpdate
        );
        const checkExists = lodash.get(objLodash, `data[${rowIndex}].idMH`);
        if (checkExists) {
            newData = lodash.set(
                { data: [...data] },
                `data[${rowIndex}].isUpdate`,
                true
            );
        }
        setData(newData.data);
    };

    //* save data into state step
    const handleSaveData = async () => {
        try {
            const cloneData = [...data];
            modifyData(cloneData);
            toast.success(i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_SUCCESS'));
        } catch (error) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_ERROR'));
            return;
        }
    };

    //* handle selected rows to delete
    const handleSelectedRow = (listSelectedRowId) => {
        const converListSelectedRowId = listSelectedRowId.map((id) =>
            Number(id)
        );
        setSelectedRowIndex(converListSelectedRowId);
    };

    //* handle delete rows selected
    const deleteRows = () => {
        let listRemoved = [];
        const newData = data.filter((row, index) => {
            if (selectedRowIndex.includes(index)) {
                listRemoved.push(row?.idMH)
                return false;
            }
            return true;
        });
        if (listRemoved.length) {
            modifyDataContainer(listRemoved);
        }
        setData(newData);
        modifyData(newData);
        setIsOpenDeleteModal(false);
    };

    const checkSubjectIsExist = (subID) => {
        for (const kkt of dataContainer) {
            for (const lkkt of kkt?.loaiKhoiKienThuc) {
                for (const gn of lkkt?.gomNhom) {
                    for (const ctgn of gn?.chiTietGomNhom) {
                        if(String(ctgn?.idMH) === String(subID)){
                            return true;
                        }
                    }
                }
            }
        }
        return false;
    }

    const openConfirmDelete = () => {
        setIsOpenDeleteModal(true);
    };

    const bodyConfirmDeleteModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    return (
        <div style={{marginTop: "10px"}}>
            <Styles>
                <div
                    style={{
                        display: "flex",
                        padding: "0 0 10px 0",
                    }}
                >
                    <Button onClick={handleSaveData} color="warning">
                        {i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE')}
                    </Button>
                    <Button
                        onClick={() => setIsOpenAddSubjectModal(true)}
                        color="success"
                        style={{
                            marginLeft: "10px",
                        }}
                    >
                        {i18n.t('FLOW_CREATE_CTDT_DETAIL.ADD')}
                    </Button>
                    <Button
                        onClick={openConfirmDelete}
                        color="danger"
                        style={{
                            marginLeft: "10px",
                        }}
                    >
                        {i18n.t('FLOW_CREATE_CTDT_DETAIL.DELETE')}
                    </Button>
                </div>
                <TablesSelectionNoiDung
                    columns={columns}
                    data={data}
                    updateMyData={updateMyData}
                    skipPageReset={true}
                    isPagination={false}
                    handleSelectedRow={handleSelectedRow}
                    listGomNhom={listGomNhom}
                    moveToSubject={moveToSubject}
                />
            </Styles>
            <ConfirmSubmit
                modal={isOpenDeleteModal}
                toggle={() => setIsOpenDeleteModal(!isOpenDeleteModal)}
                headerTitle={""}
                bodyContent={bodyConfirmDeleteModal()}
                handleOkButtonOnClick={deleteRows}
                handleCancelButtonOnClick={() =>
                    setIsOpenDeleteModal(!isOpenDeleteModal)
                }
                isModalDelete={true}
            />
            {/* <ConfirmSubmit
                modal={isOpenAddSubjectModal}
                toggle={() => setIsOpenAddSubjectModal(!isOpenAddSubjectModal)}
                headerTitle={"Thm mi mn hc"}
                bodyContent={<p></p>}
                // handleOkButtonOnClick={deleteRows}
                handleCancelButtonOnClick={() =>
                    setIsOpenAddSubjectModal(!isOpenAddSubjectModal)
                }
            /> */}
            <CreateNewSubject
                options={transformData(defaultSubjects)}
                modal={isOpenAddSubjectModal}
                toggle={() => setIsOpenAddSubjectModal(!isOpenAddSubjectModal)}
                headerTitle={""}
                handleOkButtonOnClick={addNewMonHoc}
                handleCancelButtonOnClick={() =>
                    setIsOpenAddSubjectModal(!isOpenAddSubjectModal)
                }
            />
        </div>
    );
};

export default NoiDungChuongTrinh;
