export const updateMyData = (rowIndex, columnId, value) => {
    // We also turn on the flag to not reset the page
    setSkipPageReset(true);
    setData((old) =>
        old.map((row, index) => {
            if (index === rowIndex) {
                return {
                    ...old[rowIndex],
                    [columnId]: value,
                };
            }
            return row;
        })
    );
};
export const validateOnlyNumber = (text) => {
    const regex = /^[0-9]*$/g;
    return regex.test(text);
};

export const convertToGroup = (data = [], groupTypes, groupField) => {
    const results = [];
    for (const type of groupTypes) {
        const subjectsType = data.filter(
            (item) =>
                String(item[groupField]).toUpperCase() ===
                String(type).toUpperCase()
        );
        results.push({ type, subjects: [...subjectsType] });
    }
    // console.log(`results`, results);
    return results;
};

export const transformData = (data) => {
    return data.map((subject) => {
        return {
            label: subject.tenTiengViet,
            value: subject.id,
            data: { monHoc: subject, idMH: subject.id },
        };
    });
};

export function removeItemOnce(arr, index) {
    var value = arr[index];
    if (value) {
        arr.splice(index, 1);
    }
    return arr;
}

export async function checkExistInArray (array, value) {
    for(let item of array){
        if(item?.chuanDauRa?.id === value){
          return true;
        }
        
        if(item?.children){
            const isExist = await checkExistInArray(item.children, value);
            if(isExist) {
                return isExist;
            }
        }
      
    }
    return false;
}