import React from 'react';

import './StepProgressBar.css';

const StepsForm = (props) => {
    
    return (
        <div className="container" style={{maxWidth: '100%'}}>
            <div className="multisteps-form">
                <div className="row">
                    <div className="col ml-auto mr-auto mb-2 mt-2">
                        <div className="multisteps-form__progress">
                        {
                            props?.steps && props?.steps?.map((step, index) => {
                                const activeStepProps = props?.activeStep || 0;
                                const statusStep = activeStepProps >= step?.order ? 'js-active' : activeStepProps === step?.order ? 'active' : '';

                                return <div className={`multisteps-form__progress-btn ${statusStep}`} key={index}>
                                    <div className="text-center step_label">{step?.label || ''}</div>
                            </div>
                            })
                        }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default StepsForm ;