import React, { useState } from "react";
import {
    Button,
    Modal,
    ModalFooter,
    ModalHeader,
    ModalBody,
    Label,
} from "reactstrap";
import { useTranslation } from "react-i18next";
// import InputSelectSearchable from "components/common/InputSelectSearchable/InputSelectSearchable";
import AsyncSelect from "react-select/async";
import monHocApi from "api/common/monHoc.api";
import { transformData } from "../utils";

const CreateNewSubject = (props) => {
    const [optionSelected, setOptionSelected] = useState(null);
    const [search, setSearch] = useState("");

    // useEffect(() => {
    //     setOptionSelected(null);
    // }, [props.modal]);

    const onChange = (selected) => {
        setOptionSelected(selected);
    };

    const handleOK = () => {
        if (optionSelected?.data) {
            props.handleOkButtonOnClick(optionSelected.data);
            setOptionSelected(null);
        }
        props.toggle();
    };

    const loadOptions = async (inputValue, callback) => {
        const results = await monHocApi.getAll({ searchKey: inputValue });
        callback(transformData(results?.contents) || []);
    };

    const { t } = useTranslation();

    return (
        <Modal isOpen={props.modal} toggle={props.toggle}>
            <ModalHeader toggle={props.toggle}>{props.headerTitle}</ModalHeader>
            <ModalBody>
                <div>
                    <Label>{t('FLOW_CREATE_CTDT_DETAIL.CREATE_NEW_SUBJECTS.LABEL_SUBJECT')}</Label>
                    <AsyncSelect
                        cacheOptions
                        defaultOptions={props.options}
                        loadOptions={loadOptions}
                        onChange={onChange}
                        value={props.value}
                        onInputChange={(newValue) => setSearch(newValue)}
                    />
                </div>
            </ModalBody>
            <ModalFooter>
                <Button
                    color={`${props.isModalDelete ? "danger" : "primary"}`}
                    onClick={handleOK}
                >
                    {t(props.titleOk || "admin.common.OK")}
                </Button>
                <Button
                    color="warning"
                    onClick={props.handleCancelButtonOnClick}
                >
                    {t(props.titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
};

export default CreateNewSubject;
