import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";
import Styles from "../component/styled";
import TablesSelection from "../component/TablesSelection";
import * as lodash from "lodash";
import ConfirmSubmit from "../../../../components/admin/ConfirmModal/ConfirmModal";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const KeHoachGiangDay = ({
    columns,
    subjects,
    modifyData,
    isEnableButton = false,
}) => {
    const [data, setData] = useState([]);
    const [selectedRowIndex, setSelectedRowIndex] = useState([]);
    const [isOpenModal, setIsOpenModal] = useState(false);
    // const [modalAddNewSubject, setModalAddNewSubject] = useState(false);

    useEffect(() => {
        setData(subjects);
    }, [subjects]);

    // const addNewMonHocLocal = () => {
    //     const cloneData = [...data];
    //     cloneData.push({
    //         idMH: "",
    //         ghiChu: "",
    //         monHoc: {
    //             ma: "",
    //             tenTiengViet: "",
    //             tenTiengAnh: "",
    //             soTinChi: 0,
    //             soTietLyThuyet: 0,
    //             soTietThucHanh: 0,
    //             soTietTuHoc: 0,
    //         },
    //     });
    //     setData(cloneData);
    // };

    //* update new value into state
    const updateMyData = (rowIndex, columnId, value) => {
        const newData = lodash.set(
            { data: [...data] },
            `data[${rowIndex}].${columnId}`,
            value
        );
        setData(newData.data);
    };

    //* store rows selected to delete
    const handleSelectedRow = (listSelectedRowId) => {
        const converListSelectedRowId = listSelectedRowId.map((id) =>
            Number(id)
        );
        setSelectedRowIndex(converListSelectedRowId);
    };

    //* handle remove rows selected
    const deleteRows = () => {
        const newListSubject = data.filter((row, index) => {
            if (selectedRowIndex.includes(index)) {
                return false;
            }
            return true;
        });
        setData(newListSubject);
        setIsOpenModal(false);
        modifyData(newListSubject);
    };

    //* save data into step state
    const handleSaveData = () => {
        try {
            modifyData(data);
            toast.success(i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_SUCCESS'));
        } catch (error) {
            toast.error(i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE_ERROR'));
            return;
        }
    };

    const openConfirmDelete = () => {
        setIsOpenModal(true);
    };

    const bodyConfirmDeleteModal = () => {
        return <p>{i18n.t('FLOW_CREATE_CTDT_DETAIL.MODAL_CONFIRM_CONTENT')}</p>;
    };

    return (
        <div>
            <Styles>
                {isEnableButton ? (
                    ""
                ) : (
                    <div
                        style={{
                            display: "flex",
                            padding: "0 0 10px 0",
                        }}
                    >
                        {/* <Button onClick={() => {}} color="warning">
                        Reset Data
                    </Button> */}

                        <Button onClick={handleSaveData} color="warning">
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.SAVE')}
                        </Button>
                        {/* <Button
                            onClick={() => setModalAddNewSubject(true)}
                            color="success"
                            style={{
                                marginLeft: "10px",
                            }}
                        >
                            Add row
                        </Button> */}
                        <Button
                            onClick={openConfirmDelete}
                            color="danger"
                            style={{
                                marginLeft: "10px",
                            }}
                        >
                            {i18n.t('FLOW_CREATE_CTDT_DETAIL.DELETE')}
                        </Button>
                    </div>
                )}
                <TablesSelection
                    columns={columns}
                    data={data}
                    updateMyData={updateMyData}
                    skipPageReset={true}
                    isPagination={false}
                    handleSelectedRow={handleSelectedRow}
                />
            </Styles>

            <ConfirmSubmit
                modal={isOpenModal}
                toggle={() => setIsOpenModal(!isOpenModal)}
                headerTitle={""}
                bodyContent={bodyConfirmDeleteModal()}
                handleOkButtonOnClick={deleteRows}
                handleCancelButtonOnClick={() => setIsOpenModal(!isOpenModal)}
                isModalDelete={true}
            />
        </div>
    );
};

export default KeHoachGiangDay;
